<template>
  <div class="container mt-5">
    <div class="row justify-content-center">
      <h5>üìà‰ªñÁ§æÊØîËºÉ</h5>
    </div>
    <div class="row justify-content-center">
      <apexchart class="col-md-8" type="line" :options="options" :series="series"></apexchart>
    </div>
  </div>
</template>

<script>
// json import
import electricityJson from '../assets/ratePlan_electricity.json';
import gasJson from '../assets/ratePlan_gas.json';

export default {
  name: 'Graph',
  props: ["range", "type", "x-title"], // [median, maxRange, minRange], type
  data(){
    return {
      // json„ÅÆÂÆöÁæ©
      electricityJson: electricityJson,
      gasJson: gasJson,
      // graph data
      // apexgraph data
      options: {
        chart: {
          id: 'vuechart-example',
        },
        // Ëª∏
        xaxis: {
          type: 'numeric',
          title: {
            text: this.xTitle,
            offsetY: 0,
          }
        },
        yaxis: {
          type: 'numeric',
          title: {
            text: "ÊñôÈáë / ÂÜÜ",
            offsetX: 0,
          }
        },
        // Á∑ö„ÅÆË®≠ÂÆö
        stroke: {
          show: true,
          width: 1.2
        },
        tooltip: {
          enabled: true
        }
      },
      series: [{
        data: []
      }]
    }
  },
  watch:{
    range:{
      handler:function() {
        // Â§âÊï∞ÂÆ£Ë®Ä
        let newData = []; // Êñ∞„Åó„Åè„Ç∞„É©„Éï„ÅßË°®Á§∫„Åô„Çã„Éá„Éº„Çø
        let newDataName = []; // Êñ∞„Åó„Åè„Ç∞„É©„Éï„Å´Ë°®Á§∫„Åô„Çã‰ºöÁ§æÂêç„Å®„Éó„É©„É≥Âêç
        let maxRange = this.range.maxRange; // „Ç∞„É©„ÉïxËª∏ÊúÄÂ§ßÂÄ§
        let minRange = this.range.minRange; // „Ç∞„É©„ÉïxËª∏ÊúÄÂ∞èÂÄ§
        let median = Number(this.range.median); // „Ç∞„É©„Éï„ÅÆxËª∏„ÅÆ‰∏≠ÂøÉ„ÅÆÂÄ§

        // jsonType„ÅÆÈÅ∏Êäû„Å®xËª∏„ÅÆÂÄ§„ÅÆË®≠ÂÆö
        let jsonData = '';
        if(this.type === 'electricity') {
          jsonData = this.electricityJson
          maxRange = maxRange > 1000 ? 1000 :maxRange;
          maxRange = minRange < 0 ? 0 :maxRange;
          median = median > 995 ? 995 : median;
          median = median < 5 ? 5 : median;
        }
        else if(this.type === 'gas'){
          jsonData = this.gasJson;
          maxRange = maxRange > 80 ? 80 :maxRange;
          maxRange = minRange < 0 ? 0 :maxRange;
          median = median > 75 ? 75 : median;
          median = median < 5 ? 5 : median;
        }

        // json„Éá„Éº„Çø„ÇíÂÖ®Êé¢Á¥¢
        jsonData.forEach(companyData => {
          let tmpData = []; // ÊñôÈáë‰∏ÄÊôÇÊ†ºÁ¥çArray

          newDataName.push(`${companyData.name} ${companyData.plan}`); //‰ºöÁ§æÂêç„Å®„Éó„É©„É≥Âêç„ÇíÁôªÈå≤

          // max-min„ÅÆÂπÖ„Åå10‰ª•‰∏ã„ÅÆÂ†¥Âêà„ÅØ¬±5Ë°®Á§∫„Åï„Åõ„Çã„Åü„ÇÅ„Å´„Å®„Åô„Çã
          if(maxRange - minRange < 10 ) {
            maxRange = median + 5;
            minRange = median - 5;
          }

          // min„Åã„Çâmax„ÅÆÊñôÈáë„ÇíË®àÁÆó
          for(let x = minRange; x <= maxRange; x++) {        
            
            let base = 0; // Âü∫Êú¨ÊñôÈáë
            let chargeTmp = 0; // ‰ΩøÁî®ÊñôÈáë

            companyData.using.forEach(val => {
              // ‰∏≠Â§ÆÂÄ§„Çà„ÇäÊúÄÈ´òÂÄ§„Åå‰Ωé„ÅÑÂ†¥Âêà(ÈõªÊ∞ó)
              if(val.high < x && this.type ==='electricity') {
                chargeTmp += val.charge * (val.high - val.low);
              }
              // ‰∏≠Â§ÆÂÄ§„ÅåÁØÑÂõ≤ÂÜÖ„ÅÆÂ†¥ÂêàÔºàÈõªÊ∞óÔºâ
              if(val.low < x && x <= val.high && this.type ==='electricity') {
                chargeTmp += val.charge * (x - val.low);
                base = val.base;
              }
              // ‰∏≠Â§ÆÂÄ§„ÅåÁØÑÂõ≤ÂÜÖ„ÅÆÂ†¥Âêà („Ç¨„Çπ)
              if(val.low < x && x <= val.high && this.type === 'gas') {
                chargeTmp += val.charge * x;
                base = val.base; 
              }
            })
            // Âü∫Êú¨ÊñôÈáë„ÇíÂä†„Åà„Çã
            chargeTmp += base;

            // series„Å´Ê†ºÁ¥ç„Åô„Çã„Éá„Éº„Çø„Çí„Åü„ÇÅ„Çã
            tmpData.push([x, chargeTmp.toFixed(0)]);
          }

          // ÂÖ®„Éá„Éº„ÇøÊ†ºÁ¥çÂæå„ÄÅnull„Åß„Å™„Åë„Çå„Å∞Êñ∞„Åó„Åè„Ç∞„É©„Éï„Å´Ë°®Á§∫„Åô„Çã„Éá„Éº„Çø„Å®„Åó„Å¶Ê†ºÁ¥ç
          if(tmpData !== null) {
            newData.push(tmpData);
          }
        })
        

        // Êñ∞„Åó„ÅèÊ†ºÁ¥ç„Åô„Çã„Éá„Éº„Çø„ÅÆË®≠ÂÆö
        const newSeries = newData.map((val, i) => {
          const tmpObj = {
            name : newDataName[i],
            data : val
          }
          return tmpObj;
        });
        this.series = newSeries;
      
      },
      deep: true
    } 
  } 
}
</script>
